<template>
    <button v-on:click="run" :disabled="disabled || time > 0">{{ text }}</button>
</template>
<script>
export default {
    props: {
        second: {
            type: Number,
            default: 120
        },
        disabled: {
            type: Boolean,
            default: false
        }
    },
    data:function () {
        return {
            time: 0,
            myDisabled: this.disabled
        }
    },
    watch: {
        disabled(val) {
            this.myDisabled = val;
        },
        myDisabled(val) {
            this.$emit('on-state-change', val);
        }
    },
    methods: {
        run: function () {
            this.$emit('run');
        },
        start: function(){
            this.time = this.second;
            this.timer();
        },
        stop: function(){
            this.time = 0;
            this.myDisabled = false;
        },
        timer: function () {
            if (this.time > 0) {
                this.time--;
                setTimeout(this.timer, 1000);
            }else{
                this.myDisabled = false;
            }
        }
    },
    computed: {
        text: function () {
            return this.time > 0 ? this.time + 's 后重新获取' : '获取验证码';
        }
    }
   
}
</script>


